<script>

import ConferenceMixin from './_ConferenceMixin'

export default {
    name: 'dt-conference-info',
    mixins: [ConferenceMixin],
    computed: {
        fields() {
            return [
                {
                    name: '',
                    value: this.nameFull,
                },
                {
                    name: 'Country',
                    value: this.country,
                },
                {
                    name: 'City',
                    value: this.city,
                },
                {
                    name: 'Date',
                    value: this.dateInterval,
                },
                {
                    name: 'Rank',
                    value: this.rank,
                },
            ]
        }
    },
}
</script>

<template lang="pug">

md-list.dt-conference-info(
    md-theme="th3i"
    :md-dense="true"
    :class=`{'md-primary': true,}`)
    md-divider
    md-subheader {{ isENG ? 'Conference' : 'Конференция' }}
    md-list-item(
        v-for="field of fields"
        :key="field.name"
        v-if="field.value")
        span.dt-conference-info__field
            | {{ field.name ? field.name + ': ' : ''}}{{ field.value }}
    md-divider
    md-list-item(
        v-if="site"
        :href="site"
        target="_blank")
        span.dt-conference-info__field
            | {{ isENG ? 'Conference site' : 'Сайт конференции' }}
        dkbr-icon(
            name="public")
    md-divider
</template>

<style lang="sass">

@import '~@dt/sass/root.sass'

.dt-conference-info

</style>



