<script>

import EventsMixin from './_EventsMixin'
import Slider from './../Slider'
import Event from './Event'

export default {
    name: 'dt-events',
    mixins: [EventsMixin],
    components: {
        'dt-slider': Slider,
        'dt-event': Event,
    },
}
</script>

<template lang="pug">

div.dt-events
    dt-slider.dt-events__slider(
        :titles="titlesShort"
        :ids="ids"
        :id="id"
        @select="id_ => select(id_)")
    dt-event.dt-events__event(
        :event="event_"
        :isShowContent="true")
</template>

<style lang="sass">

@import '~@dt/sass/root.sass'

.dt-events

</style>



