<script>

import EventsList from '@dt/components/events/EventsList'
import Events from '@dt/components/events/Events'

export default {
    name: 'dt-page-events',
    props: {
        id: {
            type: String,
            default: '',
        },
    },
    components: {
        'dt-events-list': EventsList,
        'dt-events': Events,
    },
}
</script>

<template lang="pug">

div.dt-page-events
    dt-events-list(
        v-if="!id")
    dt-events(
        v-if="id"
        :id="id")
</template>



