<script>

import JournalMixin from './_JournalMixin'

export default {
    name: 'dt-journal-info',
    mixins: [JournalMixin],
    computed: {
        fields() {
            return [
                {
                    name: 'Publisher',
                    value: this.journal.publisher,
                },
                {
                    name: 'ISSN',
                    value: this.journal.issn,
                },
                {
                    name: 'Country',
                    value: this.journal.country,
                },
                {
                    name: 'WoS Impact Factor (2016)',
                    value: this.journal.if,
                },
                {
                    name: 'WoS quartile',
                    value: this.journal.quartile
                        ? 'Q' + this.journal.quartile
                        : undefined,
                },
                {
                    name: 'Scopus',
                    value: this.journal.scopus
                        ? 'indexed'
                        : undefined,
                    isDivider: true,
                },
            ]
        }
    },
}
</script>

<template lang="pug">

md-list.dt-journal-info(
    md-theme="th3i"
    :md-dense="true"
    :class=`{'md-primary': true,}`)
    md-divider
    md-subheader {{ isENG ? 'Journal' : 'Журнал' }}
    md-list-item(
        v-for="field of fields"
        :key="field.name"
        v-if="field.value")
        span.dt-journal-info__field
            | {{ field.name }}: {{ field.value }}
    md-divider
    md-list-item(
        v-if="journal.site"
        :href="journal.site"
        target="_blank")
        span.dt-journal-info__field
            | {{ isENG ? 'Journal site' : 'Сайт журнала' }}
        dkbr-icon(
            name="public")
    md-divider
</template>

<style lang="sass">

@import '~@dt/sass/root.sass'

.dt-journal-info

</style>



