export default {
    props: {
        conference: {
            type: Object,
            required: true,
        },
    },
    computed: {
        nameFull() {
            if(!this.conference || !this.conference.name_full)
                return ''
            return this.conference.name_full
        },
        city() {
            if(!this.conference || !this.conference.city)
                return ''
            return this.conference.city
        },
        country() {
            if(!this.conference || !this.conference.country)
                return ''
            return this.conference.country
        },
        dateInterval() {
            if(!this.conference||!this.conference.start||!this.conference.end)
                return ''
            let format = this.isENG ? 'MMMM D, YYYY' : 'D MMMM, YYYY'
            let r1 = this.$moment(this.conference.start).format(format)
            let r2 = this.$moment(this.conference.end).format(format)
            return `${r1} - ${r2}`
        },
        rank() {
            if(!this.conference || !this.conference.rank)
                return ''
            return 'Core ' + this.conference.rank
        },
        site() {
            if(!this.conference || !this.conference.site)
                return ''
            return this.conference.site
        },
    },
}



