<script>

import PaperMixin from './_PaperMixin'
import JournalInfo from './../journals/JournalInfo'
import ConferenceInfo from './../conferences/ConferenceInfo'

export default {
    name: 'dt-paper-info',
    mixins: [PaperMixin],
    components: {
        'dt-journal-info': JournalInfo,
        'dt-conference-info': ConferenceInfo,
    },
    props: {
        paper: {
            type: Object,
            required: true,
        },
        person: {
            type: Object,
            default: _ => undefined,
        },
    },
    data() {
        return {
            isShowInfo: false,
            isCopied: false,
            errCopy: '',
        }
    },
    computed: {
        journalName() {
            if(this.paper.conference && this.paper.conference.name)
                return 'Proc. ' + this.paper.conference.name
            if(!this.paper.journal || !this.paper.journal.name)
                return this.paper.journal_old
            return this.paper.journal.name
        },
        support() {
            if(this.paper.support != 'Megagrant')
                return ``
            return this.isENG
                ? `Is supported by "Megagrant" funding`
                : `Работа выполнена при поддержке "МегаГрантом"`
        },
        authors() {
            if(!this.paper.author)
                return ''
            return this.paper.author.split(' and ').map((a, i, A) => {
                a = a.split(',')
                let name = a[1] ? a[1].trim() : ''
                name = name.length == 1 ? name + '.' : name
                let surname = a[0] && a[0] != 'others' ? a[0].trim() : ''
                let isETC = a[0] == 'others'
                let isInTeam = true
                if(!surname || !this.teamTbl.isLoaded) {
                    isInTeam = false
                }
                else {
                    if(!this.person) {
                        try { this.teamTbl.row(surname.toLowerCase()) }
                        catch(err) { isInTeam = false }
                    }
                    else {
                        isInTeam = this.person.surname == surname
                    }
                }
                let isLast = i == A.length-1
                return {name, surname, isETC, isInTeam, isLast}
            })
        },
        isMega() {
            if(this.paper.support != 'Megagrant')
                return false
            if(this.paper.affiliation != 'Skoltech')
                return false
            if(!this.paper.journal || !this.paper.journal.if)
                return false
            return true
        },
        bib() {
            let bib = []
            if(this.paper.title)
                bib.push(`title={${this.paper.title}}`)
            if(this.paper.author)
                bib.push(`author={${this.paper.author}}`)
            if(this.paper.journal && this.paper.journal.name)
                bib.push(`journal={${this.paper.journal.name}}`)
            if(this.paper.volume)
                bib.push(`volume={${this.paper.volume}}`)
            if(this.paper.number)
                bib.push(`number={${this.paper.number}}`)
            if(this.paper.pages)
                bib.push(`pages={${this.paper.pages}}`)
            if(this.paper.year)
                bib.push(`year={${this.paper.year}}`)
            if(this.paper.journal && this.paper.journal.publisher)
                bib.push(`publisher={${this.paper.journal.publisher}}`)
            if(this.paper.doi)
                bib.push(`doi={${this.paper.doi}}`)
            if(this.paper.url)
                bib.push(`url={${this.paper.url}}`)
            return `@article{${this.paper.id},\n  ` + bib.join(',\n  ') + `\n}`
        },

        IsEnabledInfo() {
            if(this.journalName == 'ArXiv preprint')
                return false
            if(this.paper.support != 'Megagrant')
                return false
            return true
        },
    },
    methods: {
        onCopy(e) {
            this.isCopied = true
        },
        onCopyError(e) {
            this.isCopied = true
        },
    },
}
</script>

<template lang="pug">

div.dt-paper-info
    dt-journal-info.dt-paper-info__journal(
        v-if="paper.journal"
        :journal="paper.journal")
    dt-conference-info.dt-paper-info__conference(
        v-if="paper.conference"
        :conference="paper.conference")
    md-list.dt-paper-info__paper(
        md-theme="th3i"
        :md-dense="true"
        :class=`{'md-primary': true,}`)
        md-divider
        md-subheader {{ isENG ? 'Publication' : 'Публикация' }}
        md-list-item(
            v-if="paper.volume")
            span.dt-paper-info__field Volume: {{ paper.volume }}
        md-list-item(
            v-if="paper.number")
            span.dt-paper-info__field Number: {{ paper.number }}
        md-list-item(
            v-if="paper.pages")
            span.dt-paper-info__field Pages: {{ paper.pages }}
        md-list-item(
            v-if="paper.doi")
            span.dt-paper-info__field Doi: {{ paper.doi }}
        md-divider
        md-list-item(
            v-if="paper.url"
            :href="paper.url"
            target="_blank")
            span.dt-paper-info__field
                | {{ isENG ? 'Publication on the publisher site' : 'Публикация на сайте издателя' }}
            dkbr-icon(
                name="public")
        md-list-item(
            @click=""
            v-clipboard:copy="this.bib"
            v-clipboard:success="onCopy"
            v-clipboard:error="onCopy")
            span.dt-paper-info__field(v-if="isENG")
                | Copy bibtex to clipboard
            span.dt-paper-info__field(v-if="isRUS")
                | Копировать BibTex в буфер
            dkbr-icon(
                name="code")
        md-divider
</template>

<style lang="sass">

@import '~@dt/sass/root.sass'

.dt-paper-info
    +flex(row, wrap, space-between, flex-start)
    &__paper
        flex: 1 0 300px
    &__journal
        flex: 1 0 300px
    &__conference
        flex: 1 0 300px
</style>



