<script>

import EventMixin from './_EventMixin'
import PaperInfo from './../papers/PaperInfo'

export default {
    name: 'dt-event',
    mixins: [EventMixin],
    components: {
        'dt-paper-info': PaperInfo,
    },
}
</script>

<template lang="pug">

dkbr-frame.dt-event(
    th="th7"
    :isThInv="true"
    :elevation="10")
    div.dt-event__header
        dkbr-icon(
            :isThInv="true"
            name="date_range"
            :text="date")
    div.dt-event__title {{ title }}
    div.dt-event__content
        dkbr-figure.dt-event__content-image(
            v-if="imgs.length",
            :srcs="imgs"
            :isAuto="true"
            :autoDt="10000"
            :animDt="1000"
            :w="500"
            :ws="350"
            :hs="400"
            :sp="200"
            :scaling="20"
            :perspective="50"
            :count="2")
        div.dt-event__content-text
            div.dt-event__content-text-item(
                v-if="get_('description')"
                v-html="get_('description')")
    dt-paper-info.dt-event__content-text-item(
        v-if="isShowPaperInfo"
        :paper="event.paper")
    div.dt-event__content-text-item
        div.dt-event__content-text-item(
            v-if="get_('description_sub')"
            v-html="get_('description_sub')")
        div.dt-event__content-text-item(
            v-if="event.paper")
            span.dt-event__subtitle Abstract.
            | &nbsp; {{ event.paper.abstract }}
    div.dt-event__footer
        div.dt-event__links
            div.dt-event__links-item(
                v-if="link"
                v-for="(link, i) in [get_('link1'), get_('link2')]")
                dkbr-icon(name="link" :size="1")
                dkbr-button(
                    :isThInv="true"
                    :isRaised="false"
                    :isDense="true"
                    :text="get_(`link${i+1}_text`)"
                    :href="link"
                    target="_blank")
            div.dt-event__links-item(
                v-if="event.paper && !isShowPaperInfo")
                dkbr-icon(name="link" :size="1")
                dkbr-button(
                    :isThInv="true"
                    :isRaised="false"
                    :isDense="true"
                    :text="isENG ? 'Publication text' : 'Текст публикации'"
                    :href="event.paper.url"
                    target="_blank")
</template>

<style lang="sass">

@import '~@dt/sass/root.sass'

.dt-event
    +flex(column)
    +padding(10px 10px 2px)
    +border(light-top)
    &__header
        +padding(2px)
        +flex(row, nowrap, space-between, center)
        +font(extra)
        +color-font(light3)
        &_small
            +font(extra-small)
    &__title
        //flex: 1 0 100px
        +padding(5px 0 20px)
        +color-font(dark3)
        +font(base-bold)
        &_link
            text-decoration: underline
            cursor: pointer
            &:hover
                +color-font(light3)
                text-decoration: none
    &__subtitle
        +color-font(dark3)
        +font(base-bold)
    &__content
        +flex(row, wrap-reverse, space-around, flex-end)
        +padding(20px)
        &-text
            flex: 1 0 300px
            &-item
                +padding(20px)
                +font(base)
                text-align: justify
        &-image
            flex: 1 0 300px
            +padding-r(20px)
    &__footer
        +flex(row, wrap, flex-end, center)
    &__links
        flex: 1 0 300px
        +flex(column, wrap, flex-start)
        &-item
            +flex(row, nowrap, flex-start, center)
</style>



