export default {
    props: {
        paper: {
            type: Object,
            required: true,
        },
    },
    computed: {
        id() {
            if(!this.paper || !this.paper.id)
                return ''
            return this.paper.id
        },
        title() {
            if(!this.paper || !this.paper.title)
                return ''
            return this.paper.title
        },
        year() {
            if(!this.paper || !this.paper.year)
                return undefined
            return this.paper.year
        },
        date() {
            if(this.paper.date)
                return this.paper.date
            if(!this.year)
                return undefined
            return `${this.year}-01-01T00:00:00.001Z`
        },
        volume() {
            if(!this.paper || !this.paper.volume)
                return ''
            return this.paper.volume
        },
        number() {
            if(!this.paper || !this.paper.number)
                return ''
            return this.paper.number
        },
        pages() {
            if(!this.paper || !this.paper.pages)
                return ''
            return this.paper.pages
        },
        doi() {
            if(!this.paper || !this.paper.doi)
                return ''
            return this.paper.doi
        },
        url() {
            if(!this.paper || !this.paper.url)
                return ''
            return this.paper.url
        },
        journalName() {
            if(!this.paper)
                return ''
            if(this.paper.conference && this.paper.conference.name)
                return 'Proc. ' + this.paper.conference.name
            if(!this.paper.journal || !this.paper.journal.name)
                return this.paper.journal_old
            return this.paper.journal.name
        },
        support() {
            if(!this.paper)
                return ''
            if(this.paper.support != 'Megagrant')
                return ``
            return this.isENG
                ? `Is supported by "Megagrant" funding`
                : `Работа выполнена при поддержке "МегаГрантом"`
        },
        authors() {
            if(!this.paper || !this.paper.author)
                return ''
            return this.paper.author.split(' and ').map((a, i, A) => {
                a = a.split(',')
                let name = a[1] ? a[1].trim() : ''
                name = name.length == 1 ? name + '.' : name
                let surname = a[0] && a[0] != 'others' ? a[0].trim() : ''
                let isETC = a[0] == 'others'
                let isInTeam = true
                if(!surname || !this.teamTbl.isLoaded) {
                    isInTeam = false
                }
                else {
                    if(!this.person) {
                        try { this.teamTbl.row(surname.toLowerCase()) }
                        catch(err) { isInTeam = false }
                    }
                    else {
                        isInTeam = this.person.surname == surname
                    }
                }
                let isLast = i == A.length-1
                return {name, surname, isETC, isInTeam, isLast}
            })
        },
        isMega() {
            if(!this.paper)
                return false
            if(this.paper.support != 'Megagrant')
                return false
            if(this.paper.affiliation != 'Skoltech')
                return false
            if(!this.paper.journal || !this.paper.journal.if)
                return false
            return true
        },
        IsEnabledInfo() {
            if(this.journalName == 'ArXiv preprint')
                return false
            if(this.paper.support != 'Megagrant')
                return false
            return true
        },
    },
}



